.main-nav {
  position: relative;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;

    @media (max-width: 1023px) {
      flex-direction: column;
    }
  }

  li {
    margin: 0 auto;

    @media (max-width: 1023px) {
      margin: 0 0 40px;
      background-color: $grey-bg-modal;
      padding: 15px;
    }
  }
}

.main-nav::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: $white;
  opacity: 0.7;
  bottom: -8px;

  @media (max-width: 1023px) {
    display: none;
  }
}

.main-nav--opened {
  @media (max-width: 1023px) {
    background-color: $white;
    display: flex;
    flex-direction: column;
    width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 4;
  }
}

.main-nav--closed {
  @media (max-width: 1023px) {
    ul {
      display: none;
    }
  }
}

.main-nav--nojs {
  @media (max-width: 1023px) {
    background-color: $white;
    width: 100%;

    ul {
      display: flex;
      flex-direction: column;
    }
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav__toggle {
  display: none;

  @media (max-width: 1023px) {
    display: block;
    border: none;
    cursor: pointer;
    width: 50px;
    height: 50px;
    background-color: $mint;
    position: relative;
    align-self: flex-end;
  }
}

.main-nav__link {
  font-size: 16px;
  line-height: 32px;
  color: $white;
  opacity: 0.7;

  @include opensans-light;
  cursor: pointer;
  position: relative;
  display: flex;

  @media (max-width: 1023px) {
    font-size: 20px;
    line-height: 140%;
    color: $grey-dark-text-modal;
    opacity: 1;

    @include opensans-regular;
  }

  &::after {
    @media (max-width: 1023px) {
      content: "";
      position: absolute;
      top: 12px;
      right: 0;
      width: 7px;
      height: 7px;
      border: 2px solid $black-btn-forward;
      border-top: transparent;
      border-left: transparent;
      transform: rotate(-45deg);
    }
  }

  &:hover {
    color: $mint;
    opacity: 1;
  }

  &:active {
    color: $mint-pale;
  }

  &:hover::after {
    border-right: 2px solid $mint;
    border-bottom: 2px solid $mint;
    transform: rotate(-45deg);
  }

  &:active::after {
    border-right: 2px solid $mint-pale;
    border-bottom: 2px solid $mint-pale;
    transform: rotate(-45deg);
  }

  &--active {
    color: $mint-dark;
  }
}

.main-nav--closed .main-nav__toggle::before {
  @media (max-width: 1023px) {
    content: "";
    position: absolute;
    display: block;
    width: 30px;
    height: 2px;
    background-color: $black;
    box-shadow: 0 8px 0 0 $black, 0 16px 0 0 $black;
    top: 16px;
    left: 10px;
  }
}

.main-nav--closed .main-nav__toggle:hover {
  background-color: $mint-dark;
}

.main-nav--closed .main-nav__toggle:active {
  background-color: $mint-total-dark;
}

.main-nav--closed .main-nav__toggle:hover::before {
  background-color: $white;
  box-shadow: 0 8px 0 0 $white, 0 16px 0 0 $white;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: $black-pale;
  box-shadow: 0 8px 0 0 $black-pale, 0 16px 0 0 $black-pale;
}

.main-nav--opened .main-nav__toggle {
  @media (max-width: 1023px) {
    background-color: $white;
    margin-bottom: 30px;
  }
}

.main-nav--opened .main-nav__toggle::before {
  @media (max-width: 1023px) {
    position: absolute;
    content: "";
    top: 24px;
    right: 12px;
    width: 30px;
    height: 1px;
    background-color: $close-button;
    transform: rotate(45deg);
  }
}

.main-nav--opened .main-nav__toggle::after {
  @media (max-width: 1023px) {
    position: absolute;
    content: "";
    top: 24px;
    right: 12px;
    width: 30px;
    height: 1px;
    background-color: $close-button;
    transform: rotate(-45deg);
  }
}

.main-nav--opened .main-nav__toggle:hover::before {
  @media (max-width: 1023px) {
    background-color: $mint;
  }
}

.main-nav--opened .main-nav__toggle:hover::after {
  @media (max-width: 1023px) {
    background-color: $mint;
  }
}

.main-nav--opened .main-nav__toggle:active::before {
  @media (max-width: 1023px) {
    opacity: 0.5;
  }
}

.main-nav--opened .main-nav__toggle:active::after {
  @media (max-width: 1023px) {
    opacity: 0.5;
  }
}
