.goods-types {
  background-color: $grey-bg;
  padding: 10% 0;

  @media (max-width: 1023px) {
    overflow: hidden;
    padding: 6% 0;
  }
}

.goods-types__wrapper {
  width: 100%;
  padding: 1% 10%;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 1023px) {
    width: 100vw;
  }

  h2 {
    display: block;
    font-size: 30px;
    line-height: 30px;
    color: $black-title;

    @include opensans-semibold;
    text-align: center;
    margin: 0 0 80px;
    padding: 0;

    @media (max-width: 1023px) {
      font-size: 24px;
      line-height: 41px;
      margin: 0 0 54px;
    }
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    justify-content: space-between;

    @media (max-width: 1023px) {
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      gap: 50px;
    }
  }

  img {
    margin: 0 0 20px;
    background-color: $grey-dark-text-modal;

    @media (max-width: 1023px) {
      width: 270px;
      height: 300px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    font-size: 22px;
    line-height: 34px;
    color: $black;

    @include opensans-semibold;
    display: block;
    text-align: center;
  }
}

.goods-types__wrapper li:first-child {
  position: relative;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    width: 180px;
    height: 180px;
    top: 160px;
    left: -65px;
    background-color: transparent;
    border: 27px solid $mint;
    border-radius: 50%;
    z-index: -1;

    @media (max-width: 1023px) {
      width: 127px;
      height: 127px;
      border: 18px solid $mint;
      top: 201px;
      left: -38px;
    }
  }
}

.goods-types__wrapper li:first-child + li {
  padding: 35px 0 0;
  position: relative;

  @media (max-width: 1023px) {
    padding: 0;
  }

  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: -19px;
    width: 47px;
    height: 47px;
    background-color: transparent;
    border: 8px solid $mint;
    z-index: 2;

    @media (max-width: 1023px) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 36px;
    right: -22px;
    width: 47px;
    height: 47px;
    background-color: transparent;
    border: 8px solid $mint;
    z-index: 2;

    @media (max-width: 1023px) {
      display: none;
    }
  }
}

.goods-types__wrapper li:last-child {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: -34px;
    right: -33px;
    width: 95px;
    height: 95px;
    background-color: transparent;
    border: 17px solid $mint;
    border-radius: 50%;
    z-index: 2;

    @media (max-width: 1023px) {
      width: 68px;
      height: 68px;
      border: 13px solid $mint;
      top: -22px;
      right: -23px;
    }
  }
}

.goods-types__wrapper li:not(:last-child) {
  margin-right: 23px;
}
