.promo__wrapper {
  width: 100%;
  padding: 1% 19%;
  display: grid;
  grid-template-rows: min-content 1fr 1fr;
  grid-template-columns: 1fr 0.8fr;
  margin-top: 55px;
  grid-column-gap: 70px;

  @media (max-width: 1023px) {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 16%;
    width: 100%;
    margin-top: 80px;
  }

  @media (max-width: 767px) {
    width: 100%;
    padding: 2% 5%;
    margin-top: 90px;
  }

  img {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    padding: 52px 0 0;

    @media (max-width: 1023px) {
      display: none;
    }
  }

  h2 {
    font-size: 46px;
    line-height: 64px;
    color: $white;

    @include opensans-bold;
    display: block;
    margin: 0 0 30px;
    padding: 122px 0 0;
    width: 100%;
    grid-row: 1 / 2;
    grid-column: 1 / 2;

    @media (max-width: 1023px) {
      padding: 0;
      margin: 0 0 25px;
      text-align: center;
      font-size: 30px;
      line-height: 42px;
    }
  }

  p {
    font-size: 20px;
    line-height: 32px;
    color: $white;
    opacity: 0.8;

    @include opensans-light;
    margin: 0;
    padding: 0;
    display: block;
    width: 100%;
    grid-row: 2 / 3;
    grid-column: 1 / 2;

    @media (max-width: 1023px) {
      padding: 0;
      text-align: center;
      font-size: 18px;
      line-height: 32px;
      margin: 0;
      max-width: 70%;
    }
  }
}

.promo__form {
  grid-row: 1 / -1;
  grid-column: 2 / 3;
  margin: 0;
  padding: 70px 0 0;

  @media (max-width: 1023px) {
    padding: 165px 0 0;
  }

  @media (max-width: 767px) {
    width: 100%;
    padding: 150px 0 0;
  }
}

.promo__wrapper--nojs {
  h2 {
    @media (max-width: 1023px) {
      color: $black-title;
      padding: 0;
      margin: 0 0 25px;
      text-align: center;
      font-size: 30px;
      line-height: 42px;
    }
  }

  p {
    @media (max-width: 1023px) {
      padding: 0;
      text-align: center;
      font-size: 18px;
      line-height: 32px;
      margin: 0;
      max-width: 70%;
      color: $black;
    }
  }
}
